
body{
	background: #fff;
}
.qy-item{
	display: block;
    width: 90%;
    margin: .6rem auto;
    border: 0;
    text-align: center;
    font-size: .5rem;
    border-bottom: .02rem solid #e8eaf1;
    padding-bottom: .2rem;
}
.qy-sex-group{
	position: absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background:rgba(144,144,144,.5);
	z-index:666;
}
.qy-sex-group .qy-sex-item{
	position: absolute;
	bottom:0;
	width:100%;
}
.qy-sex-group .qy-btn{
	width:90%;
	margin:0.2rem auto;
	background:#fff;
	border-radius:.3rem;
	padding:.4rem;
	color:#5797ED;
	font-weight: 600;
	font-size: .5rem;
	text-align: center;
}
.qy-sex-group .qy-man{
	border-radius: .3rem .3rem 0 0;
	margin-bottom:0;
	border-bottom: .02rem solid #b2b2b2
}
.qy-sex-group .qy-woman{
	border-radius: 0 0 .3rem .3rem;
	margin-top:0;
}
.qy-header-img{
	display: block;
	text-align: center;
    margin-top:.8rem;
    margin-bottom: .6rem;
}
.qy-header-img img{
	width:1.74rem;
	border-radius: 50%
}
#file{
	display: none;
}
.qy-login-btn{
	background: #f46094;
	color:#fff;
	border-radius:1rem;
	font-size: .5rem;
	width:90%;
	margin:0 auto;
}
.qy-login-btn:active{
	background:#f45555;
}	



